/**
 * @function 
 * @since v0.0.1
 * @author Malphite
 * @desc
 *
 *  <p>{@link layui.form.render} ,,</p>
 *  <p>:</p>
 *  <ul>
 *    <li>,;,,</li>
 *    <li>jsobject,</li>
 *  </ul>
 *
 */
("use strict");
layui.define(["jquery", "form"], function (exports) {

  // jquery
  if (!window.$) window.$ = layui.$;

  /**
   * render
   */
  const renderForever = layui.form.render;

  /**  formplus   --  start  */
  /**  formpluscss,css  */
  let css = document.createElement("style");
  css.type = "text/css";
  css.innerHTML = `
      [datamaxlength]:after {
        content: attr(datamaxlength);
        position: absolute;
        right: 10px;
        bottom: 10px;
        font-size: 12px;
        line-height: 38px;
        color: #666;
        user-select: none;
        pointer-events: none;
      }
      .layui-input-wrap[datamaxlength]:after{
        right: 27px;
      }
    `;
  document.getElementsByTagName("HEAD").item(0).appendChild(css);
  /**  formplus   --  end  */

  /**
   * 
   * @namespace  
   * @constant
   */
  let constant = {

    /**
     * @inner {Number} 
     */
    INTERNAL_INDEX: 0,

    /**
     * @inner {Number} 
     */
    EVENT_SERIAL: 0,

    /**
     * @inner {String}  -> .
     * @description
     *    key
     */
    separator: ".",

    /**
     * @inner {String}  -> [
     * @description
     *    key[] 
     */
    ARRAY_SEPARATOR_PREFIX: "[",

    /**
     * @inner {String}  -> ]
     * @description
     *    key[] 
     */
    ARRAY_SEPARATOR_SUFFIX: "]",

    /**
     * @inner {String} layui
     */
    LAYUI_FILTER: "lay-filter",

    /**
     * @inner {String} layuiclass
     */
    LAYUI_FORM: "layui-form",

    /**
     * @inner {String} layui 
     * @desc
     *
     *    <ul>
     *      <li> input input onporpertychange </li>
     *      <li> input onbulr </li>
     *    </ul>
     *
     */
    LAYUI_LAZY: "lay-lazy",
  };

  /**
   * @namespace 
   * @static
   */
  let util = {

    /**
     * @function util~isArray  
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isArray: function(o){
      return Object.prototype.toString.call(o) === "[object Array]";
    },

    /**
     * @function util~isArray  String
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isString: function(o){
      return Object.prototype.toString.call(o) === "[object String]";
    },

    /**
     * @function util~isArray  Function
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isFunction: function(o){
      return Object.prototype.toString.call(o) === "[object Function]";
    },

    /**
     * @function util~isArray  Date
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isDate: function(o) {
      return Object.prototype.toString.call(o) === "[object Date]";
    },

    /**
     * @function util~isArray  Object
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isObject: function(o){
      return Object.prototype.toString.call(o) === "[object Object]";
    },

    /**
     * @function util~isArray  RegExp
     * @param o {*} 
     * @returns {boolean} true for yes; false for no
     */
    isRegExp: function(o){
      return Object.prototype.toString.call(o) === "[object RegExp]";
    },

    /**
     * @function util~each 
     * @param {Object} o  
     * @param {Function} cb 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li>{@linkplain layui.each layui},,
     *        <ol>
     *          <li>,value</li>
     *          <li>key,key</li>
     *          <li>,o</li>
     *        </ol>
     *      </li>
     *      <li>,</li>
     *      <li>objectfor ...  in...,:,keykey</li>
     *      <li>,</li>
     *    </ul>
     */
    each: function(o, cb){
      let key;
      //
      if (util.isArray(o)) {
        for (key = 0; key < o.length; key++) {
          cb && util.isFunction(cb) && cb(o[key], key, o);
        }
      } else {
        /**
         * for ...  in...
         * ,keykey
         * -- 
         */
        for (key in o) {
          cb && util.isFunction(cb) && cb(o[key], key, o);
        }
      }
    },

    /**
     * @function util~join 
     * @param {Array} a 
     * @param {String} m 
     * @returns {String} 
     */
    join: function(a, m){
      if(!m) m = "";
      var res = "";
      util.each(a, function(v){
        res += String(v) + m;
      });
      // 
      if (res != "" && m) res = res.substring(0, res.length - m.length);
      return res;
    },

    /**
     * @function util~map 
     * @param {*} a 
     * @param {Function} cb 
     * @returns {Array} 
     * @desc
     *
     *    <p style = "color: #16b777;text-indent: 10px;">,</p>
     *    <ul>
     *      <li>{@linkplain util~each },object,</li>
     *      <li>,</li>
     *      <li>,</li>
     *    </ul>
     */
    map: function(a, cb){
      var res = [];
      util.each(a, function (v, k) {
        if (cb && util.isFunction(cb)) {
          res.push(cb(v, k, a));
        }
      });
      return res;
    },

    /**
     * @function util~every 
     * @param {*} o   
     * @param {Function} cb 
     * @desc
     *
     *    <p style = "color: #16b777;text-indent: 10px;">,false</p>
     *    <ul>
     *      <li>{@linkplain util~each },object,</li>
     *      <li>false</li>
     *      <li>,</li>
     *    </ul>
     */
    every: function(o, cb) {
      let key;
      //
      if (util.isArray(o)) {
        for (key = 0; key < o.length; key++) {
          if (cb && util.isFunction(cb)) {
            if (cb(o[key], key, o) === false) break;
          }
        }
      } else {
        for (key in o) {
          if (cb && util.isFunction(cb)) {
            if (cb(o[key], key, o) === false) break;
          }
        }
      }
    },

    /**
     * @function util~every 
     *  true 
     * @param {*} o   
     * @param {Function} cb  
     * @returns {Array} 
     * @desc
     *
     *    <p style = "color: #16b777;text-indent: 10px;">,true</p>
     *    <ul>
     *      <li>{@linkplain util~each },object,</li>
     *      <li>true</li>
     *      <li>,(,)</li>
     *    </ul>
     */
    filter: function(o, cb){
      var res = [];
      util.each(o, function (v, k) {
        if (cb && util.isFunction(cb)) {
          if (cb(v, k, o)) res.push(v);
        }
      });
      return res;
    },

    /**
     * @function util~indexOf objectkey
     * @param {*} a  
     * @param {*} v  
     * @returns {*}  key,, -1
     */
    indexOf: function(a, v){
      let index = -1;
      util.every(a, (a1, i) => {
        if (a1 == v) {
          index = i;
          return false;
        }
      });
      return index;
    },

    /**
     * @function util~cloneDeep 
     * @method  
     * @param {*} o 
     * @returns {*} 
     */
    cloneDeep: function (o) {
      var res;
      switch (typeof o) {
        case "undefined":
          break;
        case "string":
          res = o + "";
          break;
        case "boolean":
          res = !!o;
          break;
        case "number":
          res = o + 0;
          break;
        case "object":
          if (o == null) {
            res = null;
          } else {
            if (util.isArray(o)) {
              res = [];
              util.each(o, (v) => res.push(util.cloneDeep(v)));
            } else if (util.isDate(o)) {
              res = new Date();
              res.setTime(o.getTime());
            } else if (util.isObject(o)) {
              res = {};
              util.each(o, (v, k) => {
                res[k] = util.cloneDeep(v);
              });
            } else if (util.isRegExp(o)) {
              res = new RegExp(o);
            } else {
              res = o;
            }
          }
          break;
        default:
          res = o;
          break;
      }
      return res;
    },

    /**
     * @function util~getFormFilterName lay-filter
     * @param {HTMLElement} formItem  
     * @returns {String} {@linkplain constant~LAYUI_FILTER lay-filter }
     * @description
     *    <p style = "color: #16b777;text-indent: 10px;">lay-filter</p>
     *    <ul>
     *      <li>{@linkplain constant~LAYUI_FILTER lay-filter }</li>
     *      <li>,</li>
     *      <li>name, `layui-formplus-{@name}` </li>
     *      <li>,</li>
     *      <li>
     *        <ol>
     *          <li>layuilay-filter,</li>
     *          <li>w3cschoolname:name,name</li>
     *          <li>lay-filtername,</li>
     *        </ol>
     *      </li>
     *    <ul>
     */
    getFormFilterName(formItem) {
      let filter = formItem.getAttribute(constant.LAYUI_FILTER);
      if (filter) return filter;
      filter = "layui-formplus-" + formItem.name;
      formItem.setAttribute(constant.LAYUI_FILTER, filter);
      return filter;
    },

    /**
     * @function util~getFormName layuiname,lay-filter
     * @param {HTMLElement} item  layui
     * @return {String} layui{@linkplain constant~LAYUI_FILTER lay-filter }
     */
    getFormName: function (item) {
      let name = item.getAttribute(constant.LAYUI_FILTER);
      if (!name) {
        name = "layui-formplus" + constant.INTERNAL_INDEX++;
        item.setAttribute(constant.LAYUI_FILTER, name);
      }
      return name;
    },

    /**
     * @function util~getFormByName nameformProxy
     * @param {String} name lay-filter 
     * @return {*} formProxy
     * @desc
     *
     *    <p style = "color: #16b777;text-indent: 10px;">layui{@linkplain constant~LAYUI_FILTER lay-filter }formProxy</p>
     *    <p>:</p>
     *    <ol>
     *      <li>layui{@linkplain constant~LAYUI_FILTER lay-filter }layui,</li>
     *      <li>layuiformProxy,,</li>
     *      <li>layui {@linkplain document#contains DOM}</li>
     *    </ol>
     */
    getFormByName: function (name) {
      let res = null;
      util.every(layui.form.$children, (c) => {
        // 
        if (!document.contains(c.$ele)) {
          c.$destroy();
          if (layui.form.$children.indexOf(c) >= 0)
            layui.form.$children.splice(layui.form.$children.indexOf(c), 1);
          return;
        }
        if (c.$name == name) {
          res = c;
          return false;
        }
        return true;
      });
      return res;
    },

    /**
     * @function util~findChildren children
     * @param {Array} children 
     * @param {Function} cb ,value
     */
    findChildren: function (children, cb) {
      if (children.length == 0) return;
      util.each(children, (child) => {
        cb && cb(child);
        util.findChildren(child.children, cb);
      });
    },

    /**
     * @function util~findChildren 
     * @param {String} s XXX.YYY.ZZZ key
     * @param {Function} fn 
     * @this formProxy
     * @desc
     *
     *    <p style = "color: #16b777;text-indent: 10px;">key(eg. XXX.YYY.ZZZ),:</p>
     *    <ol>
     *      <li>XXX.YYY.ZZZ</li>
     *      <li>XXX.YYY</li>
     *      <li>XXX</li>
     *    </ol>
     */
    bubble(s, fn) {
      if (util.isString(s)) {
        let res = s;
        do {
          let k = res.lastIndexOf(constant.separator);
          if (k >= 0) res = res.substring(0, k);
          util.isFunction(fn) && fn.call(this, res, s);
        } while (res.indexOf(constant.separator) > 0);
      }
    },

    /**
     * @function util~getValue keyformProxy
     * @this formProxy
     * @param {String} k XXX.YYY.ZZZ key
     * @param {*} v 
     * @returns {*} formProxy
     * @desc
     *    <ol>
     *      <li>keyformProxy</li>
     *      <li>,</li>
     *      <li>,</li>
     *    </ol>
     */
    getValue(k, v) {
      let [currentContext, flag, parent, key] = [this, true];
      util.every(k.split(constant.separator), (v1) => {
        let r = currentContext[v1];
        parent = currentContext;
        key = v1;
        // 
        if (/\[\d+\]/.test(v1))
          r =
            currentContext[
              v1.substring(0, v1.indexOf(constant.ARRAY_SEPARATOR_PREFIX))
              ][
              v1.substring(
                v1.indexOf(constant.ARRAY_SEPARATOR_PREFIX) + 1,
                v1.indexOf(constant.ARRAY_SEPARATOR_SUFFIX)
              )
              ];
        if (r === undefined) {
          flag = false;
          return false;
        }
        currentContext = r;
        return true;
      });
      // 
      if (flag && v !== undefined) {
        parent[key] = v;
        return v;
      }
      return flag ? currentContext : null;
    },
    /**
     * @function util~observe 
     * @this  formProxy
     * @param {*} o  
     * @return {*} 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;">,</p>
     *    <ol>
     *      <li>target: </li>
     *      <li>key: target,'a.b.c[d]' </li>
     *    </ol>
     *    <p style = "color: #16b777;text-indent: 10px;">,{@linkplain util~doObserve }</p>
     */
    observe(o) {
      let self = this;
      // 
      if (util.isArray(o.target)) {
        o.target.__self__ = self;
        o.target.__key__ = o.key;
        o.target.__proto__ = arrayMethods;
        util.each(o.target, (v, k) => {
          v = util.doObserve.call(self, {
            target: o.target,
            value: v,
            name: k,
            key: o.key ? o.key : k,
          });
        });
        return o.target;
      }
      if (!o.target || !util.isObject(o.target)) return o.target;
      // 
      util.each(o.target, (v, k) => {
        util.doObserve.call(self, {
          target: o.target,
          value: v,
          name: k,
          key: o.key,
        });
      });
      return o.target;
    },
    /**
     * @function util~doObserve 
     * @this  formProxy
     * @param {*} o 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;">,</p>
     *    <ol>
     *      <li>target: </li>
     *      <li>value: </li>
     *      <li>name: key</li>
     *      <li>key: ,'a.b.c[d]' </li>
     *    </ol>
     */
    doObserve(o) {
      // name
      let self = this,
        fKey = o.key ? o.key + constant.separator + o.name : o.name;
      // ,
      let _proxy = util.observe.call(self, {
        target: o.value,
        key: fKey,
      });
      // 
      self.dispatcher.add(fKey);
      Object.defineProperty(o.target, o.name, {
        configurable: true,
        enumerable: true,
        get() {
          self.dispatcher &&
          self.dispatcher.get.call(self, fKey, _proxy, o.name);
          return _proxy;
        },
        set(value) {
          if (value === _proxy) return;
          let oldValue = _proxy;
          self.dispatcher.before.call(self, fKey, value, oldValue, o.name);
          _proxy = util.observe.call(self, {
            target: value,
            key: fKey,
          });
          self.dispatcher.after.call(self, fKey, value, oldValue, o.name);
        },
      });
    },
  };

  /**
   * @namespace AOP 
   * @static
   */
  let AOP = {
    /**
     * @constructor
     * @function AOP~Dispatcher 
     */
    Dispatcher: function () {

      let self = this;

      /**
       * @inner 
       * @desc
       *
       *    {@linkplain constant.EVENT_SERIAL };
       * length0
       */
      this.enableAccess = [];

      /**
       * @inner 
       * @desc
       *     {@linkplain AOP.Aspect } ,key
       */
      this.coreMap = {};

      /**
       * @function Dispatcher~addListener 
       * @param {Function} fn    
       * @param {Boolean} flag    true   false 
       * @param {Number} serial  
       * @param {Function} cb    
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;"> -- :</p>
       *    <ol>
       *      <li></li>
       *      <li>setted</li>
       *    </ol>
       */
      this.addListener = (fn, flag = false, serial, cb) => {
        // 
        util.each(self.coreMap, (v) =>
          v.add(fn, flag ? "deep" : "bind", serial, cb)
        );
        // 
        self.enableAccess.splice(self.enableAccess.indexOf(serial), 1);
        // ,
        if (self.enableAccess.length == 0)
          util.each(self.coreMap, (v) => v.offline());
      };
      /**
       * @function Dispatcher~watch 
       * @param {*} obj     
       * @param {String} key    key
       * @param {Function} fn     
       * @param {Boolean} deep   
       * @param {Boolean} immediate  
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;"></p>
       *    <ul>
       *      <li> {@linkplain AOP.Dispatcher.addListener }</li>
       *      <li>,</li>
       *    </ul>
       */
      this.watch = (obj, key, fn, deep = false, immediate = false) => {
        // 
        if (!self.coreMap[key]) self.add(key);
        self.coreMap[key].add(fn, deep ? "deep" : "bind", null, null, true);
        // 
        if (immediate) fn.call(obj, util.getValue.call(obj, key), null, key);
      };

      /**
       * @function Dispatcher~add 
       * @param {String} key key
       */
      this.add = (key) => {
        if (!self.coreMap[key]) self.coreMap[key] = new AOP.Aspect(key);
      };

      /**
       * @function Dispatcher~get 
       * @this formProxy
       * @param {String} key key
       * @param {*} v   
       * @param {*} k   key(,key)
       */
      this.get = function (key, v, k) {
        // 
        self.enableAccess.length > 0 &&
        self.coreMap[key].online(self.enableAccess);
      };

      /**
       * @function Dispatcher~before 
       * @deprecated
       * @this formProxy
       * @param {*} key  key
       * @param {*} v    
       * @param {*} v1   
       * @param {*} k    key()
       */
      this.before = function (key, v, v1, k) {};

      /**
       * @function Dispatcher~after 
       * @this formProxy
       * @param {String} key  key
       * @param {*} v    
       * @param {*} v1   
       * @param {*} k    key(,key)
       */
      this.after = function (key, v, v1, k) {
        let _this = this;
        // 1. ,   false 
        self.coreMap[key].pool = util.filter(
          self.coreMap[key].pool,
          function (event) {
            return false !== event.fn.call(_this, v, v1, k, key);
          }
        );
        // 2.
        util.bubble(key, (v) => {
          if (v !== key && self.coreMap[v])
            util.each(
              self.coreMap[v].pool,
              (e) => "deep" === e.type && e.fn.call(_this, v, v1, k, key)
            );
        });
      };
    },
    /**
     * @constructor
     * @function AOP~Aspect 
     */
    Aspect: function (key) {

      let self = this;

      /**
       * @inner 
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;">,XXX.YYY.ZZZkey</p>
       */
      this.key = key;

      /**
       * @inner 
       * @type {boolean}
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;">:true;false</p>
       */
      this.access = false;

      /**
       * @inner 
       * @type {Number[]}
       * @see constant~EVENT_SERIAL
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;"></p>
       */
      this.serial = [];

      /**
       * @inner  {@linkplain AOP~Event }
       * @type {AOP~Event[]}
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;">{@linkplain AOP~Event }</p>
       */
      this.pool = [];

      /**
       * @function Aspect~online 
       * @param {Number[]} serial 
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;"></p>
       *    <ol>
       *      <li>{@linkplain Aspect~access }true</li>
       *      <li>{@linkplain Aspect~serial },</li>
       *    </ol>
       *
       */
      this.online = (serial) => {
        self.access = true;
        util.each(serial, (s) => {
          if (self.serial.indexOf(s) < 0) self.serial.push(s);
        });
      };

      /**
       *  @function Aspect~online 
       *  @desc
       *    <p style = "color: #16b777;text-indent: 10px;"></p>
       *    <ol>
       *      <li>{@linkplain Aspect~access }false</li>
       *      <li>{@linkplain Aspect~serial }</li>
       *    </ol>
       */
      this.offline = () => {
        self.access = false;
        self.serial = [];
      };

      /**
       * @function Aspect~add {@linkplain Aspect~pool }
       * @param {Function} fn  
       * @param {String} type   (deep|bind)
       * @param {Number} serial  
       * @param {Function} cb  
       * @param {Function} flag  ,
       */
      this.add = (fn, type, serial, cb, flag) => {
        // :(watch)
        if (flag) return self.pool.push(new AOP.Event(fn, type));
        // 
        if (!self.access) return false;
        if (!!serial && self.serial.indexOf(serial) >= 0) {
          self.pool.push(new AOP.Event(fn, type));
          cb && cb(self.key);
        } else if (!serial) {
          self.pool.push(new AOP.Event(fn, type));
          cb && cb(self.key);
        }
      };
    },
    /**
     * @constructor
     * @function AOP~Event 
     * @param {Function} fn 
     * @param {String} type  (deep|bind) deep
     */
    Event: function (fn, type = "bind") {
      this.fn = fn;
      this.type = type;
    },
  };

  /**
   * @constructor  
   * @param {*} options 
   * @returns {formProxy} formProxy
   * @desc
   *    <p style = "color: #16b777;text-indent: 10px;"></p>
   *    <ul>
   *      <li style = "color: #ff5722;">(formplus),</li>
   *      <li></li>
   *      <li>:
   *        <ol>
   *          <li>formProxy.$data - object,namekey;value</li>
   *          <li>formProxy.$watch - </li>
   *          <li>formProxy.$destroy - </li>
   *        </ol>
   *      </li>
   *    </ul>
   */
  let formProxy = function (options) {
    return new formProxy.fn.newInstance(options);
  };

  formProxy.prototype = formProxy.fn = {

    /**
     * {@linkplain formProxy }
     * @param options
     * @returns {formProxy} 
     */
    newInstance: function (options) {
      // 
      this.dispatcher = new AOP.Dispatcher();
      // 
      this._initData(options.data, options.dataSource);
      // 
      this._initMethods(options.methods);
      // 
      this._initWatchs(options.watchs);
      // 
      this.isAlive = true;
      /**
       * @method ()
       * @see util~getValue
       * @param k
       * @param v
       * @returns {*}
       */
      this.getValue = function (k, v) {
        return util.getValue.call(this, k, v);
      };
      /**
       * @method 
       * @param {*} fn 
       * @desc
       *    <p style = "color: #16b777;text-indent: 10px;">,</p>
       *    <ul>
       *      <li>,XXX.YYY.ZZZkey{@linkplain util~getValue} </li>
       *      <li>,,,{@linkplain Dispatcher~addListener}</li>
       *    </ul>
       */
      this.addListener = function (fn) {
        let res = null,
          serial = constant.EVENT_SERIAL++;
        const f = util.isFunction(fn);
        if (f) this.dispatcher.enableAccess.push(serial);
        res = f ? fn.apply(this) : util.getValue.call(this, fn);
        if (f) this.dispatcher.addListener(fn, false, serial);
        return res;
      };
      // created
      options.created && options.created.apply(this);
      return this;
    },

    /**
     * @method obj
     * @param {*} obj
     * @param {*} k
     * @param {*} v
     * @returns
     */
    $set(obj, k, v) {
      return this._set.call(this, obj, k, v);
    },

    /**
     * 
     * @param {*} key
     * @param {*} value
     * @returns
     */
    $watch(key, value) {
      return this._watch.call(this, key, value);
    },

    /**
     * 
     */
    $destroy() {
      util.each(this.$children, (v) => v.$destroy());
      // 
      this.isAlive = false;
    },

    /**
     * @function formProxy.fn~_initData data
     * @see util~observe
     * @param dataOption
     * @param dataSource
     * @private
     */
    _initData(dataOption = {}, dataSource = {}) {
      let self = this;
      // object,
      let data = util.isFunction(dataOption)
        ? dataOption.call(self, dataSource)
        : dataOption;
      // 
      self.$data = util.observe.call(self, { target: data });
      // 
      Object.keys(self.$data).forEach((key) => {
        Object.defineProperty(self, key, {
          configurable: true,
          enumerable: true,
          get() {
            return self.$data[key];
          },
          set(value) {
            if (value == self.$data[key]) return;
            self.$data[key] = value;
          },
        });
      });
    },

    /**
     * @function formProxy.fn~_initMethods 
     * @param methods
     * @private
     */
    _initMethods(methods = {}) {
      let self = this;
      util.each(methods, (v1, k1) => (self[k1] = v1));
    },

    /**
     * @function formProxy.fn~_initMethods 
     * @param watchs
     * @private
     */
    _initWatchs(watchs = {}) {
      let self = this;
      util.each(watchs, (v, k) => self._watch.call(self, k, v));
    },

    _watch(key, fn) {
      let self = this;
      if (util.isFunction(fn)) {
        self.dispatcher.watch(self, key, fn);
      } else {
        self.dispatcher.watch(self, key, fn.handler, fn.deep, fn.immediate);
      }
    },

    _set(obj, k, v) {
      let self = this;
      if (util.isFunction(obj)) {
        obj.apply(self);
      } else {
        let o = {};
        o[k] = v;
        obj = Object.assign(obj, o);
      }
      self.$data = util.observe.call(self, { target: self.$data });
      Object.keys(self.$data).forEach((key) => {
        Object.defineProperty(self, key, {
          configurable: true,
          enumerable: true,
          get() {
            return self.$data[key];
          },
          set(value) {
            if (value == self.$data[key]) return;
            self.$data[key] = value;
          },
        });
      });
    },
  };

  formProxy.fn.newInstance.prototype = formProxy.fn;

  /**
   * @namespace formRenderer  
   */
  let formRenderer = {
    /**
     * @function formRenderer~listen 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li>{@linkplain layui.form}$children,{@linkplain formProxy }</li>
     *      <li>{@linkplain layui.form.render},:
     *        <ol>
     *          <li>,,undefined,</li>
     *          <li>filter()</li>
     *          <li>{@linkplain formRenderer.render },formplus</li>
     *          <li></li>
     *          <li>{@linkplain formProxy },,</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    listen: function () {
      /**
       * form{@linkplain formProxy }
       */
      if (!layui.form.$children) {
        layui.form.$children = [];
      } else {
        // ,
        return false;
      }
      /**
       * render
       * @param {*} type    
       * @param {*} filter  formfilter
       * @param {*} deploy  formplus,false
       * @returns layui.form; {@linkplain formProxy }
       */
      layui.form.render = function (type, filter, deploy = false) {
        if (!deploy) return renderForever.call(layui.form, type, filter);
        // 
        let selector = filter
          ? `.${constant.LAYUI_FORM}[${constant.LAYUI_FILTER}="${filter}"]`
          : `.${constant.LAYUI_FORM}`;
        let res = null;
        document.querySelectorAll(selector).forEach((ele) => {
          if (ele instanceof HTMLElement) {
            // vform
            if (ele.getAttribute("formplus") == null) {
              ele.setAttribute("formplus", "true");
              // 1. lay-filter name
              let name = util.getFormName(ele);
              // 2. 
              res = util.getFormByName(name) || formRenderer.render(ele);
            }
            // render
            renderForever.call(layui.form, type, filter);
          }
        });
        return res;
      };
    },

    /**
     * @function formRenderer~render 
     * @param {HTMLElement} item form
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;">layuiform{@linkplain formProxy }</p>
     *    <ol>
     *      <li>lay-filterkey</li>
     *      <li>{@linkplain formProxy }</li>
     *      <li>,</li>
     *    </ol>
     */
    render: function (item) {
      // 1.form - lay-filter 
      let name = util.getFormName(item);
      // 3.{@linkplain formProxy }
      let proxy = new formProxy({
        data: {},
        /**
         * @method 
         * @this formProxy 
         * @desc
         *    <p style = "color: #16b777;text-indent: 10px;">dom,</p>
         *    <ol>
         *      <li>dom,</li>
         *      <li>dom,dom(layui,layui)</li>
         *    </ol>
         *
         */
        created() {
          let self = this;
          // namedom,nameformProxy;eledom,
          self.$name = name;
          self.$ele = item;
          // 
          let fieldElems = $(item).find("input,select,textarea");
          util.each(fieldElems, (formItem) => {
            if (formItem instanceof HTMLElement) {
              // lay-ignore,
              if (formItem.getAttribute("lay-ignore") === null) {
                // namekey
                let _name = formItem.name;
                // layui , name
                if (_name) {
                  // typecheckbox
                  let _type =
                    formItem.type == "checkbox" ? "checkbox" : formItem.type == "radio" ? "radio" : formItem.tagName.toLowerCase() == "select" ? "select" : "input";
                  // switch 
                  if (
                    formItem.type == "checkbox" &&
                    formItem.getAttribute("lay-skin") == "switch"
                  )
                    _type = "switch";
                  // namekey
                  if (!self.$data[_name])
                    self.$set(self.$data, _name, "checkbox" == _type ? [] : "");
                  // 
                  Array.prototype.every.call(
                    formRenderer.getRenderers(),
                    function (r) {
                      if (r.canDeal(formItem, _type)) {
                        r.deal(item, formItem, self);
                        return false;
                      }
                      return true;
                    }
                  );
                }
              }
            }
          });
        },
      });
      // form
      layui.form.$children.push(proxy);
      // 
      return proxy;
    },

    /**
     * @constructor
     * @function formRenderer~renderer 
     * @param {*} condition ,
     * @param {*} action   
     */
    renderer: function (condition, action) {
      // ,condition,
      this.canDeal = util.isFunction(condition)
        ? (...param) => condition.apply(this, param)
        : (...param) => new Function(condition, param);
      this.deal = (...param) => action.apply(this, param);
    },

    /**
     * @function formRenderer~getRenderers 
     * @returns {formRenderer~renderer []}
     */
    getRenderers: function () {
      // 
      if (!formRenderer.renderers) formRenderer.initRenderers();
      return formRenderer.renderers;
    },

    /**
     * @function formRenderer~initRenderers 
     * @returns {formRenderer~renderer []}
     */
    initRenderers: function () {
      if (formRenderer.renderers) return formRenderer.renderers;
      formRenderer.renderers = [];
      formRenderer.renderers.push(MyRenderers.input);
      formRenderer.renderers.push(MyRenderers.date);
      formRenderer.renderers.push(MyRenderers.switch);
      formRenderer.renderers.push(MyRenderers.checkbox);
      formRenderer.renderers.push(MyRenderers.radio);
      formRenderer.renderers.push(MyRenderers.select);
      return formRenderer.renderers;
    },
  };

  /**
   * @namespace MyRenderers  
   */
  let MyRenderers = {
    /**
     * @inner MyRenderers~input 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li style = "color: #ff5722;">:
     *        <ol>
     *          <li>typeinput</li>
     *          <li>laydate,formplus</li>
     *          <li>layui-laydate-id | lay-key,laydate</li>
     *        </ol>
     *      </li>
     *      <li>:
     *        <ol>
     *          <li>,</li>
     *          <li>input,</li>
     *          <li>,</li>
     *          <li>23</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    input: new formRenderer.renderer(
      function (formItem, type) {
        // ,laydate
        return (
          type == "input" &&
          formItem.getAttribute("laydate") === null &&
          formItem.getAttribute("lay-key") === null &&
          formItem.getAttribute("layui-laydate-id") === null
        );
      },
      function (item, formItem, formProxy) {
        // 
        formProxy.getValue(formItem.name, formItem.value);

        let eventKey =
          formItem.getAttribute(constant.LAYUI_LAZY) != null
            ? "blur"
            : "input propertychange";
        // input or textarea 
        // input textarea 
        util.each(eventKey.split(" "), (v) => {
          formItem.addEventListener(v, function () {
            formProxy.getValue(formItem.name, this.value);
          });
        });

        /**
         * 
         *    maxlength 
         *
         *  1. 
         *  2.  
         *  3. 
         *  4. 
         *  5. 
         *
         *  lay-lazy
         */
        if (formItem.getAttribute("maxlength") != undefined) {
          // lay-affix="number" input,,
          if(formItem.getAttribute("lay-affix") == "number")
            renderForever.call(layui.form, $(formItem));
          // 1. 
          let maxlength = formItem.getAttribute("maxlength");
          // 2. 
          let _tempName = formItem.name + "-limit";
          formProxy.$set(formProxy.$data, _tempName, "0/" + maxlength);
          // 4. 
          formProxy.$watch(formItem.name, {
            immediate: true,
            deep: false,
            handler: function (v) {
              let length = v ? v.length : 0;
              // ,:after
              formItem.parentElement.setAttribute(
                "dataMaxlength",
                "" + length + "/" + maxlength
              );
            },
          });
        }

        /**
         *  lay-affixnumber
         *     lay-affix
         *  
         */
        if (
          formItem.getAttribute("lay-affix") == "number" ||
          formItem.getAttribute("lay-affix") == "clear"
        ) {
          layui.form.on(
            "input-affix(" + util.getFormFilterName(formItem) + ")",
            function (data) {
              var elem = data.elem; //  DOM 
              formProxy.getValue(formItem.name, elem.value);
            }
          );
        }

        // 
        formProxy.$watch(formItem.name, function (v) {
          formItem.value = v;
        });
      }
    ),
    /**
     * @inner MyRenderers~date 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li style = "color: #ff5722;">:
     *        <ol>
     *          <li>typeinput</li>
     *          <li>laydate,formplus</li>
     *          <li>layui-laydate-id,laydate</li>
     *        </ol>
     *      </li>
     *      <li>:
     *        <ol>
     *          <li>laydate,lay-options</li>
     *          <li>layui-laydate-id|lay-keykey,</li>
     *          <li>done</li>
     *          <li>,</li>
     *          <li>34</li>
     *          <li>,</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    date: new formRenderer.renderer(
      function (formItem, type) {
        let flag =
          type == "input" &&
          (formItem.getAttribute("laydate") !== null ||
            formItem.getAttribute("layui-laydate-id") !== null);
        return flag;
      },
      function (item, formItem, formProxy) {
        if (
          formItem.getAttribute("layui-laydate-id") === null &&
          formItem.getAttribute("lay-key") === null
        ) {
          let optionsAttr = formItem.getAttribute("lay-options");
          let options = optionsAttr ? JSON.parse(String(optionsAttr).replace(/\'/g, () => '"')) : {};
          options.elem = formItem;
          layui.laydate.render(options);
        }
        let laydateKey =
          formItem.getAttribute("layui-laydate-id") ||
          formItem.getAttribute("lay-key");
        // laydate
        let laydateInstance = layui.laydate.getInst(laydateKey);
        /**
         * done
         */
        let done = laydateInstance.config.done;
        let newDone = function (value, date, endDate, p) {
          if (p) value = p.value;
          let params = {
            value: value,
            date: date,
            endDate: endDate,
          };
          done && done(value, date, endDate, params);
          formProxy.getValue(formItem.name, params.value);
        };
        laydateInstance.config.done = newDone;

        // 
        formProxy.getValue(formItem.name, formItem.value);

        // 
        formProxy.$watch(formItem.name, function (v) {
          formItem.value = v;
        });
      }
    ),
    /**
     * @inner MyRenderers~checkbox 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li style = "color: #ff5722;">: switch </li>
     *      <li>:
     *        <ol>
     *          <li>,checkboxArray,chacked</li>
     *          <li>formcheckbox,</li>
     *          <li>,,</li>
     *          <li>23</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    checkbox: new formRenderer.renderer(
      function (formItem, type) {
        return (
          type == "checkbox" && formItem.getAttribute("lay-skin") != "switch"
        );
      },
      function (item, formItem, formProxy) {
        // 
        item
          .querySelectorAll('[name="' + formItem.name + '"]')
          .forEach((checkbox) => {
            if (checkbox.checked)
              formProxy.$data[formItem.name].push(checkbox.value);
          });
        /**
         * layuidomchange
         * layuionlay-filter
         * lay-filternamefilter
         *
         * checkboxradiolayui
         */
        layui.form.on(
          "checkbox(" + util.getFormFilterName(formItem) + ")",
          function () {
            formItem.parentElement
              .querySelectorAll('[name="' + formItem.name + '"]')
              .forEach((checkbox) => {
                // 
                let _data = formProxy.getValue(formItem.name);
                // 
                if (checkbox.checked) {
                  if (util.indexOf(_data, checkbox.value) < 0)
                    _data.push(checkbox.value);
                } else {
                  if (util.indexOf(_data, checkbox.value) >= 0)
                    _data.splice(util.indexOf(_data, checkbox.value), 1);
                }
              });
          }
        );

        // 
        formProxy.$watch(formItem.name, function (v) {
          item
            .querySelectorAll('[name="' + formItem.name + '"]')
            .forEach((checkbox) => {
              if (util.indexOf(v, checkbox.value) >= 0) {
                checkbox.checked = true;
                checkbox.setAttribute("checked", "checked");
              } else {
                checkbox.checked = false;
                checkbox.removeAttribute("checked");
              }
            });
          layui.form.render(
            "checkbox",
            item.getAttribute(constant.LAYUI_FILTER)
          );
        });
      }
    ),
    /**
     * @inner MyRenderers~radio 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li style = "color: #ff5722;">: type = "radio" </li>
     *      <li>:
     *        <ol>
     *          <li>,chacked</li>
     *          <li>formradio,</li>
     *          <li>,,</li>
     *          <li>23</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    radio: new formRenderer.renderer(
      function (formItem, type) {
        return type == "radio";
      },
      function (item, formItem, formProxy) {
        // 
        let value = "";
        item
          .querySelectorAll('[name="' + formItem.name + '"]')
          .forEach((radio) => {
            if (radio.checked) value = radio.value;
          });
        formProxy.$data[formItem.name] = value;
        /**
         * layuidomchange
         * layuionlay-filter
         * lay-filternamefilter
         *
         * checkboxradiolayui
         */
        layui.form.on(
          "radio(" + util.getFormFilterName(formItem) + ")",
          function () {
            formItem.parentElement
              .querySelectorAll('[name="' + formItem.name + '"]')
              .forEach((radio) => {
                // 
                if (radio.checked)
                  formProxy.getValue(formItem.name, radio.value);
              });
          }
        );
        // 
        formProxy.$watch(formItem.name, function (v) {
          item
            .querySelectorAll('[name="' + formItem.name + '"]')
            .forEach((radio) => {
              if (v == radio.value) {
                radio.checked = true;
                radio.setAttribute("checked", "checked");
              } else {
                radio.checked = false;
                radio.removeAttribute("checked");
              }
            });
          layui.form.render("radio", item.getAttribute(constant.LAYUI_FILTER));
        });
      }
    ),
    /**
     * @inner MyRenderers~select 
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;"></p>
     *    <ul>
     *      <li style = "color: #ff5722;">: type = "select" </li>
     *      <li>:
     *        <ol>
     *          <li>,</li>
     *          <li>formselect,</li>
     *          <li>,,</li>
     *          <li>23</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    select: new formRenderer.renderer(
      function (formItem, type) {
        return type == "select";
      },
      function (item, formItem, formProxy) {
        // 
        formProxy.$data[formItem.name] = formItem.value;
        /**
         * layuidomchange
         * layuionlay-filter
         * lay-filternamefilter
         */
        layui.form.on(
          "select(" + util.getFormFilterName(formItem) + ")",
          function () {
            // 
            formProxy.getValue(formItem.name, formItem.value);
          }
        );

        // 
        formProxy.$watch(formItem.name, function (v) {
          formItem.value = v;
          formItem.querySelectorAll("option").forEach((option) => {
            if (v == option.value) {
              option.setAttribute("selected", "selected");
            } else {
              option.removeAttribute("selected");
            }
          });
          layui.form.render("select", item.getAttribute(constant.LAYUI_FILTER));
        });
      }
    ),
    /**
     * @inner MyRenderers~switch switch
     * @desc
     *    <p style = "color: #16b777;text-indent: 10px;">switch</p>
     *    <ul>
     *      <li style = "color: #ff5722;">: checkbox</li>
     *      <li>:
     *        <ol>
     *          <li>,checked</li>
     *          <li>formswitch,</li>
     *          <li>,,</li>
     *          <li>23</li>
     *        </ol>
     *      </li>
     *    </ul>
     */
    switch: new formRenderer.renderer(
      function (formItem, type) {
        return (
          type == "switch" && formItem.getAttribute("lay-skin") == "switch"
        );
      },
      function (item, formItem, formProxy) {
        // 
        item
          .querySelectorAll('[name="' + formItem.name + '"]')
          .forEach((checkbox) => {
            formProxy.$data[formItem.name] = checkbox.checked;
          });
        /**
         * layuidomchange
         * layuionlay-filter
         * lay-filternamefilter
         *
         * checkboxradiolayui
         */
        layui.form.on(
          "switch(" + util.getFormFilterName(formItem) + ")",
          function () {
            formProxy.getValue(formItem.name, this.checked);
          }
        );

        // 
        formProxy.$watch(formItem.name, function (v) {
          item
            .querySelectorAll('[name="' + formItem.name + '"]')
            .forEach((checkbox) => {
              if (formProxy.getValue(formItem.name) == true) {
                checkbox.checked = true;
                checkbox.setAttribute("checked", "checked");
              } else {
                checkbox.checked = false;
                checkbox.removeAttribute("checked");
              }
            });
          layui.form.render(
            "checkbox",
            item.getAttribute(constant.LAYUI_FILTER)
          );
        });
      }
    ),
  };

  /**    -- start ---   */
  /**
   * <p style = "color: #ff5722;" >warning:,</p>
   */
  var arrayProto = Array.prototype;
  var arrayMethods = Object.create(arrayProto);
  var methodsToPatch = [
    "push",
    "pop",
    "shift",
    "unshift",
    "splice",
    "sort",
    "reverse",
  ];
  methodsToPatch.forEach(function (method) {
    var original = arrayProto[method];
    Object.defineProperty(arrayMethods, method, {
      value: function () {
        var formProxy = this.__self__;
        if (!formProxy) return original.apply(this, arguments);
        var key = this.__key__;
        // TODO
        var proxy = util.cloneDeep(this);
        original.apply(proxy, arguments);
        return formProxy.getValue(key, proxy);
      },
      enumerable: !!0,
      writable: true,
      configurable: true,
    });
  });
  /**    -- end ---   */

  // 
  formRenderer.listen();

  exports("formplus", {});
});
